import Popup from './Popup.js';

class PopupDelete extends Popup {
  constructor(popupSelector, submitForm) {
    super(popupSelector);
    this._submitForm = submitForm;
    this._form = this._popup.querySelector('.popup__form');
    this._deleteButton = this._form.querySelector('.popup__delete-button');
  }

  setEventListeners() {
    super.setEventListeners();
    this._form.addEventListener('submit', event => {
      event.preventDefault();
      this._deleteButton.textContent = `${this._deleteButton.textContent}...`;
      this._submitForm({ card: this._card, id: this._id });
    });
  }

  open({ card, cardID: id }) {
    super.open();
    this._card = card;
    this._cardID = id;
  }
}

export default PopupDelete;
